<template>
  <div class="content_box">
      <app-table-utils :utils="utils">
          <el-input disabled v-model="queryData.group" slot="query-1"/>
          <el-button slot="query" class="queryBtn" >查询</el-button>
          <el-button slot="add" class="queryBtn" @click="showDialog({type:'add'})">添加</el-button>
        </app-table-utils>
        <div class="table-line"></div>
         <app-tables :table="table">
            <template slot="appTable">
                <tree-table :data="table.data" :cols="table.cols" expand-all>
                    <template slot="operation" slot-scope="{data}">
                        <template v-if="data.col.key === 'operation'">
                            <el-button  type="text"  @click="showDialog({type:'addChild',data:data.row})">
                                添加下级
                            </el-button>
                            <el-button type="text" @click="showDialog({type:'update',data:data.row})">
                                编辑
                            </el-button>
                            <el-button type="text" @click="handleDelete(data.row)">
                                删除
                            </el-button>
                        </template>
                        <template v-else>
                            {{ data.row[data.col.key] || '-' }}
                        </template>
                    </template>
                </tree-table>
            </template>
        </app-tables>
         <group-form ref="GroupForm" @refresh="init()" />
  </div>
</template>
<script>
import GroupForm from './form/GroupForm';
export default {
  name:'GroupMange',
   components: {
    GroupForm
  },
  data(){
    return {
        utils: { //表格工具栏
              left: [
                  { label: '归属分组', slot: 'query-1' },
                  { slot: 'query' },
                  { slot: 'add' },
              ],
              cols: [24]
          },
        queryData:{
          pageNum:1,
          pageSize:10,
          name:null,
          group: '下拉框'
        },
        table: {
              data: [], //表格数据
              total: 0, //总页数
              page: 1, //页码索引
              tableType: 3, //表格类型
              hidePagination: true, //分页显示隐藏
               cols: [ //表格列配置
                    // {
                    //     key: '序号',
                    //     label: '序号',
                    //     width:'100px'
                    // },
                    {
                        key: 'group',
                        label: '名称'
                    },
                    {
                        key: 'remark',
                        label: '备注'
                    },
                    {
                        key: 'operation',
                        width: '250px',
                        align :'center',
                        label: '操作',
                        operation: true
                    }
                ]
          }
    }
  },
  created(){
    this.init();
  },
  methods:{
    /**
     * 初始化数据
     */
      init(){
        this.table.data = [
          {
            id:1,
            upGroup:'四川省旅游景区-上级',
            group:'四川省旅游景区',
            remark:'备注',
            children:[
               {
                id:11,
                upGroup:'四川省旅游景区',
                group:'泸州景区',
                remark:'备注',
              },
              {
                id:12,
                upGroup:'四川省旅游景区',
                group:'内江景区',
                remark:'备注',
              },
               {
                 id:13,
                upGroup:'四川省旅游景区',
                group:'宜宾市景区',
                remark:'备注',
                children:[
                  {
                    id:131,
                    upGroup:'宜宾市景区',
                    group:'竹海旅游景区',
                    remark:'备注',
                  }
                ]
              }
            ]
          }
        ]

      },

     /**
       * 分页器回调
       */
      handleCurrentChange(page) {
          this.queryData.pageNum = page;
          this.init();
      },

       /**
	     * 弹出层控制
	     */
        showDialog ({ type, id, data }) {
            if(type === 'add') {
                this.$refs.GroupForm.init({ type, title: '新增分组' });
            } else if (type === 'addChild') {
                this.$refs.GroupForm.init({ type, id, data, title: '新增子集' });
            } else if (type === 'update') {
                this.$refs.GroupForm.init({ type, id, data, title: '修改分组' });
            }
        },

          /**
       * 删除
       */
      handleDelete({ id  }) {
          this.$confirm(`此操作将会删除此条数据,是否继续?`, '提示', {
              cancelButtonText: '取消',
              confirmButtonText: '确定',
              type: 'warning',
              center: true,
              customClass: 'bg-warning'
          }).then(async () => {
              // const res = await deleteOneMsg({ id  });
              // if(res.code == '1'){
              //     this.$message.success('删除成功');
              //     this.init();
              //   }else{
              //     // 失败
              //     this.$message.error('删除失败');
              //   }
          }).catch(() => {});
      },

  }
}
</script>





